/* Box sizing rules */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
    margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
    list-style: none;
}

/* Set core root defaults */
html:focus-within {
    scroll-behavior: smooth;
}

/* Set core body defaults */
body {
    min-height: 100vh;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
    text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
    max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
    font: inherit;
}

html {
    height: 100%;
}

body {
    -webkit-font-smoothing: antialiased;
    display: flex;
    min-height: 100%;
    padding: 0;
    position: relative;
}

#root {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-height: 100%;
    width: 100%;
}

body.overflow-hidden {
    overflow: hidden !important;
}

hr {
    border-top-color: #eff1fb;
    margin: 8px 0;
}

.slick-arrow {
    background: transparent;
    border: 0 none;
    cursor: pointer;
    display: block;
    font-size: 0;
    height: 29px;
    line-height: 0;
    padding: 0;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    width: 29px;
}

.slick-prev {
    left: 15px;
}

.slick-next {
    right: 15px;
}

.slick-disabled {
    display: none !important;
}

.slick-next:before,
.slick-prev:before {
    color: #b6bcd0;
    font-family: 'Font Awesome 5 Free';
    font-size: 20px;
    line-height: 25px;
}

.slick-next:before,
.slick-prev:before {
    border-radius: 100%;
    border: 1px solid #ecedf0;
    display: block;
    font-weight: 900;
    height: 29px;
    width: 29px;
}

.slick-prev:before {
    content: '\f104';
}

.slick-next:before {
    content: '\f105';
}

.slick-slider {
    padding: 0 44px;
}

.Toastify__toast {
    min-height: inherit !important;
}

.Toastify__close-button--default {
    background-color: #f8fcff;
    border-radius: 100%;
    border: 1px solid #f1f7fd;
    box-shadow: inset 0 1px 29px 0 rgba(111, 87, 170, 0.02),
        4px 8px 14px 0 rgba(49, 48, 122);
    height: 31px;
    margin-top: -16px;
    position: absolute;
    right: 8px;
    top: 50%;
    width: 31px;
}

#divToPrint {
    color: #000;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
}

svg:not(:root) {
    overflow: hidden;
}

.popover {
    z-index: 2000;
}

#root>div {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

}

form .MuiFormControl-root,
form .MuiButton-root {
    margin-bottom: 16px;
}

.MuiCardContent-root:last-child {
    padding-bottom: 16px !important;
}

.MuiDataGrid-root--densityStandard .MuiDataGrid-cell {
    padding-bottom: 7px;
    padding-top: 7px;
}

.transposed {
    display: -ms-grid;
    -ms-grid-rows: auto auto;
    -ms-grid-columns: auto auto;
}

.transposed thead {
    -ms-grid-row: 2;
    -ms-grid-column: 1;
}

.transposed tbody {
    -ms-grid-row: 2;
    -ms-grid-column: 2;
}

.transposed tfoot {
    -ms-grid-row: 2;
    -ms-grid-column: 3;
}

.transposed caption {
    -ms-grid-row: 1;
    -ms-grid-column: 1;
    -ms-grid-column-span: 2;
}

/* Everyone Else's Grid */
@supports (display: grid) {
    .transposed {
        display: grid;
        grid-template-rows: auto auto;
        grid-template-areas: "foot body";
    }

    .transposed tbody {
        grid-area: body;
    }

    .transposed tfoot {
        grid-area: foot;
    }
}

/* Flex - Cross Browser CSS */
.transposed thead,
.transposed tbody,
.transposed tfoot {
    display: flex;
}

.transposed tr {
    display: flex;
    flex-direction: column;
}

.transposed td,
.transposed th .transposed caption {
    display: block;
}

.transposed tfoot td {
    line-height: 1.5714285714285714 !important;
    font-size: 0.875rem !important;
}